import React, { useState } from "react";
import "../styles/Api.css";

function Api() {
  const [message, setMessage] = useState("");
  const [status, setStatus] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(false);
  const [responseData, setResponseData] = useState(null);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);
    setSuccess(false);
    setResponseData(null);

    try {
      const response = await fetch("https://portfolio-backend-jet-phi.vercel.app/api/messages", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ message, status }),
      });

      if (response.ok) {
        setSuccess(true);
        setResponseData({
          message: message,
          status: status,
          timestamp: new Date().toLocaleString()
        });
        // Clear form inputs
        setMessage("");
        setStatus("");
      } else {
        throw new Error(`HTTP ${response.status}: ${response.statusText}`);
      }
    } catch (error) {
      console.error(error);
      setError(`Failed to send message: ${error.message}`);
    } finally {
      setIsLoading(false);
    }
  };

  const copyToClipboard = (text) => {
    navigator.clipboard.writeText(text);
  };

  return (
    <div id="api">
      <div className="api-container">
        <div className="api-header">
          <div className="api-icon">⚡</div>
          <h1>API Testing Dashboard</h1>
          <p>Test Robert&apos;s backend API endpoints with real-time validation</p>
        </div>

        <div className="api-cards">
          {/* API Endpoint Card */}
          <div className="api-card endpoint-card">
            <div className="card-header">
              <span className="card-icon">🔗</span>
              <h3>API Endpoint</h3>
            </div>
            <div className="endpoint-info">
              <div className="endpoint-url">
                <code>POST /api/messages</code>
              </div>
              <div className="endpoint-full">
                <span>https://portfolio-backend-jet-phi.vercel.app/api/messages</span>
                <button 
                  className="copy-btn"
                  onClick={() => copyToClipboard("https://portfolio-backend-jet-phi.vercel.app/api/messages")}
                  title="Copy URL"
                >
                  📋
                </button>
              </div>
              <a
                href="https://portfolio-backend-jet-phi.vercel.app/api/messages"
                target="_blank"
                rel="noopener noreferrer"
                className="view-data-btn"
              >
                📊 View All Messages
              </a>
            </div>
          </div>

          {/* Test Form Card */}
          <div className="api-card form-card">
            <div className="card-header">
              <span className="card-icon">🧪</span>
              <h3>Test API Request</h3>
            </div>
            <form onSubmit={handleSubmit} className="api-form">
              <div className="form-group">
                <label htmlFor="message">
                  <span className="label-icon">💬</span>
                  Message Content
                </label>
                <input
                  type="text"
                  id="message"
                  value={message}
                  onChange={(e) => setMessage(e.target.value)}
                  placeholder="Enter your test message..."
                  required
                  className="form-input"
                />
              </div>
              
              <div className="form-group">
                <label htmlFor="status">
                  <span className="label-icon">📊</span>
                  Status Type
                </label>
                <select
                  id="status"
                  value={status}
                  onChange={(e) => setStatus(e.target.value)}
                  required
                  className="form-select"
                >
                  <option value="">Choose status...</option>
                  <option value="success">✅ Success</option>
                  <option value="error">❌ Error</option>
                  <option value="info">ℹ️ Info</option>
                  <option value="warning">⚠️ Warning</option>
                </select>
              </div>
              
              <button type="submit" disabled={isLoading} className="submit-btn">
                {isLoading ? (
                  <>
                    <span className="spinner"></span>
                    Sending Request...
                  </>
                ) : (
                  <>
                    <span>🚀</span>
                    Send API Request
                  </>
                )}
              </button>
            </form>
          </div>

          {/* Response Card */}
          {(success || error || responseData) && (
            <div className="api-card response-card">
              <div className="card-header">
                <span className="card-icon">📋</span>
                <h3>API Response</h3>
              </div>
              <div className="response-content">
                {success && (
                  <div className="response-success">
                    <div className="status-indicator success">
                      <span>✅</span>
                      <span>Request Successful</span>
                    </div>
                    {responseData && (
                      <div className="response-details">
                        <div className="detail-item">
                          <strong>Message:</strong> {responseData.message}
                        </div>
                        <div className="detail-item">
                          <strong>Status:</strong> {responseData.status}
                        </div>
                        <div className="detail-item">
                          <strong>Sent:</strong> {responseData.timestamp}
                        </div>
                      </div>
                    )}
                  </div>
                )}
                
                {error && (
                  <div className="response-error">
                    <div className="status-indicator error">
                      <span>❌</span>
                      <span>Request Failed</span>
                    </div>
                    <div className="error-details">
                      {error}
                    </div>
                  </div>
                )}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default Api;
